.about {
  @extend %clearfix;

  &__left, &__right{
    position: relative;
    float: left;
    width: 50%;
  }

  &__left {
    background: $white;
  }

  &__section-title {
    @include centered('x');
    position: relative;
    top: -35px;
  }

  &__right {
    background: $beige;
  }

  &__triangles{
    position: absolute;
    bottom: 100%;
  }
}