.title {
  color: $main-font-color;
  @include sansus();
  @include font-size(3.15); //60px
  text-transform: uppercase;
  line-height: 1;
  padding: .75em 1.333em;
  display: inline-block;
  position: relative;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  @include adaptive(smart) {
    @include font-size(1.6);
  }

  $margin: .667em;

  &:after, &:before {
    content:"";
    display: block;
    @include centered('y');
    background-color: rgba($main-font-color, 0.3);
    width: 0.367em;
    height: 0.083em;
  }
  &:after {
    right: $margin;
  }
  &:before {
    left: $margin;
  }
}